body {
    background-color: var(--home-bg-color);
}

header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 22;
}

nav .container-fluid {
    z-index: 22;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    height: var(--navbar-height);
    background-color: var(--navbar-bg-color);
    width: 100%;
    box-shadow: 1px 2px 7px 6px var(--navbar-shadow-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 24px;
}

.navbar--icon {
    font-size: 1.4rem;
}

.nav-item .nav-link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    text-align: center;
}

.nav-item {
    padding-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 8px;
    width: 84px;
    border-radius: 3px;
    transition: all .2s;
}

.nav-item:hover {
    background-color: #eaeaeaad;
    transition: all .2s;
}

.navbar--options__right {
    list-style: none;
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 24px;
    width: 30%;
    right: 0;
}

.navbar--user__name {
    display: flex;
    margin-left: auto;
    margin-right: 12px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 1rem;
    font-weight: 400;
    align-items: flex-end;
    letter-spacing: 2.5px;
}

.separate:after {
    content: "";
    border-radius: 10px;
    position: absolute;
    height: 130%;
    bottom: 50%;
    transform: translateY(50%);
    left: -24px;
    width: 3px;
    background-color: rgb(144, 144, 144);
    margin: 0 12px;
}

.narvbar--logout__btn {
    position: relative;
    transition: all .1s;
    margin-left: 12px;
    border-radius: 5px;
    font-weight: 600;
    letter-spacing: 1px;
    color: rgb(255, 255, 255);
    /* background: -webkit-linear-gradient(left, #ECFDFF, #41F0D1);
    background: -o-linear-gradient(left, #ECFDFF, #41F0D1);
    background: -moz-linear-gradient(left, #ECFDFF, #41F0D1);
    background: linear-gradient(left, #ECFDFF, #41F0D1); */
    background-color: #40e7c898;
}

.narvbar--logout__btn:hover {
    transition: all .1s;
    color: #fff;
    background: var(--light-green-bold);
    background: -webkit-linear-gradient(to right, #ECFDFF, #41F0D1);
    background: -o-linear-gradient(to right, #ECFDFF, #41F0D1);
    background: -moz-linear-gradient(to right, #ECFDFF, #41F0D1);
    background: linear-gradient(to right, #ECFDFF, #41F0D1);
}

.navbar--list__bg {
    display: none;
}

.wrapper--navbar__list {
    position: fixed;
    z-index: 9999999999;
    top: 0;
    transition: all .2s;
    animation: navbarToLeft 1s;
    left: -100%;
}

.navbar--list {
    list-style: none;
    overflow-y: scroll;
    flex: 1;
    padding: 0 12px;
}

.wrapper--navbar__list.navbar--list__toggle {
    position: fixed;
    z-index: 9999999999;
    transition: all .3s;
    animation: navbarToRight .3s;
    display: flex;
    top: 0;
    left: 0;
    height: 100%;
    flex-direction: column;
    background-color: #FFF;
    min-width: 250px;
    box-shadow: 1px 2px 7px 6px var(--navbar-shadow-color);
}

.navbar--list__toggle~.navbar--list__bg {
    position: fixed;
    display: flex;
    background-color: rgba(233, 233, 233, 0.575);
    left: 0;
    opacity: 1;
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 0;
    bottom: 0;
}

.navbar--list__Logo {
    cursor: pointer;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
    /* animation: logoRotate 5s infinite linear; */
}

.navbar--list__Logo img {
    height: 100%;
}

.navbar--list li .navbar--items {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 0 10px 22px;
    cursor: pointer;
    font-size: 1.2rem;
    margin: 8px 0;
    border-radius: 3px;
    color: var(--navbar-text-color);
    text-decoration: none;
}

.navbar--items a {
    display: flex;
    color: var(--navbar-text-color);
    align-items: center;
    text-decoration: none;
    width: 100%;
}

.navbar--items__content {
    display: flex;
    align-items: center;
}

.navbar--items.active {
    background: -webkit-linear-gradient(to right, #e1f7f9, #dbdbdbad);
    background: -o-linear-gradient(to right, #e1f7f9, #dbdbdbad);
    background: -moz-linear-gradient(to right, #e1f7f9, #dbdbdbad);
    background: linear-gradient(to right, #e1f7f9, #dbdbdbad);
}

.navbar--list .navbar--items i {
    margin-right: 12px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    color: var(--navbar-text-color);
    background-color: #bf9ce62a;
}

.navbar--items:hover {
    background: -webkit-linear-gradient(to right, #eef8f9, #e9e8e8ad);
    background: -o-linear-gradient(to right, #eef8f9, #e9e8e8ad);
    background: -moz-linear-gradient(to right, #eef8f9, #e9e8e8ad);
    background: linear-gradient(to right, #eef8f9, #e9e8e8ad);
}

.navbar--items:hover a,
.navbar--items:hover i {
    color: var(--blue-light-text-bold);
    /* color: red; */
}

.navbar--items:last-child {
    margin-bottom: 0;
}

.navbar--items:first-child {
    margin-top: 0;
}

.navbar--list~hr {
    margin: 24px 0;
    color: var(--br-bg-color);
    background-color: var(--br-bg-color);
    height: 1px;
    border: none;
}


.navbar--list:last-child {
    flex: 0.4;
    overflow: hidden;
    margin-top: auto;
}

.navbar--list.top {
    margin-top: 0;
    margin-bottom: auto;
}


.wrapper--container {
    width: 100%;
}


.wrapper--flip__card {
    position: fixed;
    display: flex;
    flex-wrap: wrap;
    height: 95%;
    justify-content: space-between;
    width: 100%;
    /* padding: 10px; */
    z-index: -1;
}


.flip--card__created__Datetime {
    display: flex;
    flex-direction: column;
    padding-bottom: 24px;
}


.card--qr {
    display: flex;
    height: 40px;
    width: 40px;
    margin: 10px 12px;
    font-size: 2.5rem;
    cursor: pointer;
    margin-left: auto;
}

.flip--card__inner {
    position: relative;
    background-color: #fff;
    color: #252525;
    margin: 24px 0 12px;
    border-radius: 10px;
    width: 100%;
    height: 85%;
    margin: 0 auto;
    box-shadow: 1px 1px 4px 5px rgba(0, 0, 0, 0.089);
    display: flex;
    justify-content: center;
    transition: all 0.5s ease-out;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transform-style: preserve-3d;
}


.front,
.back {
    display: flex;
    position: absolute;
    justify-content: center;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: hidden;
    flex-shrink: 1;
    width: 100%;
    height: 100%;
}

/* background-color: rgb(140, 64, 104); */
.front {
    padding: 20px 0;
    z-index: 1;
}

.back {
    padding: 20px 0;
    font-size: 4rem;
    color: #252525;

    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -ms-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.wrapper--front__infor {
    display: flex;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    padding: 24px 12px 10px;
}

.flip--card__img {
    display: flex;
    flex-direction: column;
    margin-bottom: auto;
    position: absolute;
    width: 120px;
    overflow: hidden;
    padding-right: 12px;
}

figure img {
    width: 100%;
    object-fit: cover;
    background-size: cover;
    background-repeat: no-repeat;
}

figure figcaption {
    padding-top: 12px;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.card--user__infor {
    position: absolute;
    width: calc(100% - 120px);
    display: flex;
    flex-direction: column;
    top: 0;
    right: 0;
    padding: 24px 12px 0 12px;
}

.card--title {
    display: flex;
    margin: 0 auto;
    position: relative;
    top: 0px;
    margin-bottom: 12px;
}

.card--items__title {
    width: 80px;
    color: var(--text-color-bold);
    text-align: justify;
    margin-right: 18px;
}

.card--items__title span {
    width: 100%;
    height: 1px;
    display: inline-block;
}

.card--description .card--description__p {
    display: flex;
    flex: 1;
    font-size: .8rem;
    justify-content: flex-start;
}

.card--description {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

/* 
.card--description .card--title {
    flex-shrink: 1;
    flex: 0.5;
    padding-right: 12px;
    text-align: justify;
}

.card--title span {
    width: 100%;
    height: 1px;
    display: inline-block;
} */

.card--school__description {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: auto;
    font-size: .8rem;
    letter-spacing: 2px;
    padding: 10px 20px;
    text-align: center;
    font-weight: 600;
}

.wrapper--back__infor {
    width: 100%;
}

.card--reload {
    position: absolute;
    display: flex;
    z-index: 1;
    height: 100%;
    width: 100%;
    background-color: rgb(233, 233, 233);
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: var(--navbar-text-color)
}

.wrapper--back__infor {
    position: absolute;
    display: flex;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 24px 12px;
    justify-content: center;
}

.table--back__infor {
    display: flex;
    width: 80%;
    height: 80%;
    border: 2px solid #000;
    flex-wrap: wrap;
}

.table--back__infor__row {
    width: 100%;
    display: flex;
    font-size: 2rem;
    border: 2px solid #000;
}

.table--back__infor__items {
    display: flex;
}

.table--row__header {
    padding-left: 20px;
    display: flex;
    align-items: center;
    width: 170px;
    border-right: 4px solid #000;
    flex-shrink: 0;
}

.table--row__description {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    height: 100%;
    padding-left: 24px;
}

.modal {
    pointer-events: none;
    z-index: -1;
    position: fixed;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: all 0.4s ease-in-out;
}

.modal--qr {
    pointer-events: all;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 100px 0;
}

.modal--contain {
    position: absolute;
    height: 100%;
    width: 100%;
}


.modal--qr__img {
    box-shadow: 1px 2px 12px 4px rgb(0, 0, 0, .1);
    width: 250px;
    object-fit: cover;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 40px;
}

.modal.open {
    display: block;
    position: fixed;
    top: 0;
    z-index: 20;
    background-color: rgb(241, 241, 241);
    height: 100%;
    width: 100%;
    opacity: 1;
    animation: QROpen .5s ease-in-out;
}

.modal--qr__controller {
    width: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btn--close__mobile {
    font-size: 2.4rem;
    color: var(--red-color);
    padding: 10px 10px;
    transition: all 0.2s;
}


.btn--update {
    height: 38px;
    width: 38px;
    border-radius: 50%;
    font-size: 1.1rem;
    color: #fff;
    background-color: var(--light-green-bold);
    padding: 10px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
}

.btn--update:hover {
    cursor: pointer;
    background-color: var(--light-green-hover);
}

.btn--close__mobile:hover {
    cursor: pointer;
    color: var(--red-color-hover)
}

.card--time {
    display: flex;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.flip--card__created__Datetime {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card--time .card--time__expdate {
    color: var(--red-color);
}

.wrapper--post__container {
    position: absolute;
    z-index: -1;
}


.post--container {
    margin: 25px 0;
    padding: 0 12px;
    width: 100%;
    display: flex;
    z-index: 0;
    border-radius: 20px;
    position: relative;
    padding: 5px 12px;
    height: auto;
    font-size: 1.1rem;
    line-height: 1.4rem;
    letter-spacing: 0.05rem;
    padding-bottom: 10px;
}

.post--container:hover {
    background-color: #8e8e8e11;
}


.post--header__img img {
    position: absolute;
    width: 100%;
    background-repeat: no-repeat;
    object-fit: cover;
    background-size: cover;
    height: 100%;
}




.clamp {
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    overflow-wrap: break-word;
}



.post--contain .read-more-less--more,
.post--contain .read-more-less--less {
    outline: none;
    border-radius: 3px;
    border: none;
    padding: 2px 5px;
    font-size: 0.8rem;
    position: absolute;
    cursor: pointer;
    top: 100%;
    right: 0;
    animation: showText 1s ease-in-out;
}

/* admin screen */

.wrapper--group__container {
    height: 100%;
    width: 100%;
    padding: 20px 40px
}

.header--groupmanagement {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}

.header--groupmanagement .container--title {
    font-size: 2.2rem;
    color: var(--navbar-text-color);
}

.header--groupmanagement .group--icon {
    font-size: 1.4rem;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    margin: 0 10px;
    cursor: pointer;
    color: #fff;
}


.header--groupmanagement .group--add {
    background-color: var(--light-green-bold);
    transition: all .2s
}

.header--groupmanagement .group--add:hover {
    background-color: rgb(100, 225, 202);

}

.header--groupmanagement .group--remove {
    background-color: var(--red-color);
    transition: all .2s
}

.header--groupmanagement .group--remove:hover {
    background-color: rgb(208, 0, 0);
    transition: all .2s
}

.group--list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    margin-left: -10px;
    margin-top: 20px;
}

.group--items {
    position: relative;
    width: calc(33% - 20px);
    display: flex;
    flex-direction: column;
    background-color: rgb(235, 235, 235);
    border-radius: 5px;
    padding: 10px 10px;
    margin: 10px 0;
    margin-left: 20px;
    cursor: pointer;
    transition: all .2s;
    flex: 1 1 1;
    overflow: hidden;
}

.group--items:hover {
    background-color: rgb(226, 226, 226);
}

.group--items h3 {
    color: var(--blue-light-text-bold);
    padding-bottom: 5px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
}

.group--description {
    display: flex;
    flex-direction: column;
}

.group--items span {
    font-size: .8rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


.group--items p {
    white-space: wrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.group--description {
    margin-top: auto;
}

.group-modal {
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s;
    height: 100%;
    width: 100%;
}

.group-modal.open {
    left: 0;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 1;
}

.group-modal .modal--contain {
    position: absolute;
    z-index: 9;
    width: 600px;
    max-height: 700px;
    height: auto;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px 20px;
}

.group-modal.open .modal--group__bg {
    background: #000;
    opacity: .2;
    height: 100%;
    width: 100%;
    z-index: 2;
    pointer-events: none;
    cursor: default;
}

.modal--contain__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 15px;
}

.modal--contain__header .group--remove {
    font-size: 1.2rem;
    width: 33px;
    height: 33px;
    line-height: 40px;
    border-radius: 50%;
    margin: 0 10px;
    cursor: pointer;
    color: #fff;
    align-items: center;
    display: flex;
    justify-content: center;
}

.modal--contain__header .group--remove {
    background-color: var(--red-color);
    transition: all .2s
}

.modal--contain__header .group--remove:hover {
    background-color: rgb(208, 0, 0);
    transition: all .2s
}

.group-form .wrapper--input {
    width: 100%;
    display: flex;
}

.group-form .wrapper--input .input--label {
    display: flex;
    flex: 0.3;
    color: var(--text-color-bold);
}

.group-form .wrapper--input .input__fields {
    display: flex;
    flex: 1;
    height: 1rem;
    outline: 0;
    padding: 10px 5px;
    border: 2px solid rgb(190, 190, 190);
    border-radius: 3px;
}

.group-form .wrapper--input .input__fields:focus {
    border: 2px solid rgb(138, 205, 169);
}

.group-form .wrapper--input textarea {
    min-height: 250px;
    max-height: 250px;
}

.btn.group--btn {
    height: 40px;
    width: 100%;
    border-radius: 8px;
    color: var(--blue-light-text-bold);
    transition: all 0.2s;
}

.group--btn.primary {
    background-color: var(--navbar-items);
}

.group--btn.primary:hover {
    background-color: rgb(204, 203, 203);
    transition: all 0.2s;
}

.group--btn__remove {
    background-color: var(--red-color);
    padding: 5px 12px;
    border-radius: 5px;
    outline: 0;
    border: none;
    color: #fff;
    margin-bottom: 20px;
    cursor: pointer;
}

.group--btn__remove:hover {
    background-color: var(--red-color-hover)
}

