.wrapper--login {
    width: 100%;
    height: 100%;
}

.login--container {
    padding: 20px 0;
    min-height: 100vh;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    object-fit: cover;
    display: flex;
    margin: auto;
    background-position: center center;
}

.login--form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 500px;
    border-radius: 10px;
    background-color: var(--login-bg-color);
    padding: 65px 55px 54px;
}

.login--form__title {
    font-size: 3rem;
    color: #333;
    line-height: 1.2;
    padding-bottom: 4rem;
}

.wrapper--input {
    position: relative;
    width: 100%;
}

.wrapper--input__username {
    width: 100%;
}

.wrapper--input .input--label {

    font-size: 0.9rem;
    color: #333;
    line-height: 1.5;
    padding-left: 7px;
    font-weight: 700;
}

.wrapper--input {
    margin-bottom: 23px;
}

.wrapper--fields {
    display: flex;
    font-size: 16px;
    color: var(--br-bg-color);
    line-height: 1.2;
    width: 100%;
    height: 55px;
    padding: 0 7px 0 43px;
    align-items: center;
    justify-content: center;
    /* position: relative; */
}

.wrapper--fields i {

    left: 30px;
    width: 43px;
    margin-left: -20px;
}

.wrapper--fields input {
    height: 100%;
    margin-left: auto;
    flex: 1;
    outline: none;
    border: none;
    color: #7F7F7F;
    letter-spacing: .5px;
}

.input--focus {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--br-bg-color);
}

.login--input__fields:focus~.input--focus {
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    /* z-index: 1; */
    border-bottom: 2px solid #7F7F7F;
    content: attr(data-symbol);
    color: #adadad;
    animation: toRight .3s ease-in-out;
    outline: none;
}

.wrapper--fields:focus-within .login--form__icon {
    transition: .4s;
    color: #a64bf4;
    outline: none;
}

.login--form__policy {
    width: 100%;
    display: flex;
    justify-items: start;
    padding-bottom: 38px;
}

.login--form__link:hover {
    text-decoration: underline;
    text-decoration-color: var(--text-color);
}


div .login--form__link {
    margin-top: 24px;
    color: var(--text-color);
    cursor: pointer;
    ;
}

.btn--container {
    color: var(--text-color);
}

/* .btn--container .login--signup__link:hover {
    text-decoration: underline;
} */

.btn--container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}


.btn--container .btn--primary__bg {
    height: 50px;
}

.btn--login {
    top: 0;
    font-size: 1.1rem;
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    padding: 8px 24px;
    height: 50px;
    border: none;
    cursor: pointer;
    letter-spacing: .05rem;
    box-shadow: 0 5px 30px 0 rgb(3 216 222 / 20%);
    -moz-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, .2);
    -webkit-box-shadow: 0 5px 30px 0 rgb(3 216 222 / 20%);
    -o-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, .2);
    -ms-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, .2);
    background-color: #00dbde;
}

.btn--login:hover {
    background-color: #00bdc0;
}


.wrapper--signup {
    width: 100%;
    height: 100%;
}

.signup--container {
    /* background: url('../images/bg-01.jpg') center / cover; */
