    align-items: center;
}

.signup--form__link:hover {
    text-decoration: underline;
    text-decoration-color: var(--text-color);
}


div .signup--form__link {
    margin-left: 10px;
    color: var(--text-color);
    cursor: pointer;
}

input[type="checkbox"] {
    cursor: pointer;
}

.wrapper--btn {
    width: 100%;
    display: flex;
    margin: 0 auto;

}

.btn--container {
    color: var(--text-color);
}

.btn--container .login--signup__link-wrapper {
    display: flex;
    flex-direction: column;
}

.login--signup__link {
    text-decoration: none;
    padding: 5px 0;
}

.btn--container .login--signup__link:hover {
    text-decoration: underline;
}

.btn--container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}


.btn--container .btn--primary__bg {
    height: 50px;
}

.btn--signup {
    top: 0;
    font-size: 1.1rem;
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
