    -webkit-box-shadow: 0 5px 30px 0 rgb(3 216 222 / 20%);
    -o-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, .2);
    -ms-box-shadow: 0 5px 30px 0 rgba(3, 216, 222, .2);

}

.signup--form__policy input:checked~.btn--signup:hover {
    background-color: #00bdc0;
}

.btn--container.resetpassword {
    display: flex;
    justify-content: flex-end;
}

.btn--resetpassword.btn--resetpassword__primary__bg {
    top: 0;
    font-size: 1.1rem;
