    letter-spacing: .05rem;
    margin-left: auto;
    background-color: #00bdc0;
    cursor: pointer;
    pointer-events: all;
    z-index: 2000;
    position: relative;
}

.btn--resetpassword.btn--resetpassword__primary__bg:hover {
    background-color: #00a3a6;
    /* background-color: red; */

