/*--CSS Desktop width >= 1024px  --*/
@media only screen and (min-width:64rem) {
    .btn--close__mobile {
        display: none;
    }

    .btn--close__mobile.btn--close__pc {
        display: block
    }

    .wrapper--flip__card {
        display: flex;
        justify-content: center;
        top: 0;
        padding-top: 50px;
    }

    .flip--card__inner {
        display: flex;
        height: 90%;
        width: 600px;
        margin: 50px;
    }

    .card--description .card--description__p {
        display: flex;
        flex: 1;
        font-size: 1.1rem;
        justify-content: flex-end;
    }

    .table--row__header {
        width: 100px;
    }

    .table--back__infor__row {
        font-size: 1.2rem;
    }

    .table--row__header {
        padding: 0;
        text-align: center;
        flex-wrap: wrap;
    }

    .table--row__description {
        display: flex;
        flex-direction: column;
    }
}

/*--CSS Tablet width >= 740px and width < 1024px --*/
@media only screen and (min-width:46.25rem) and (max-width:65rem) {

    body {
        font-size: 0.8rem;
    }

    .wrapper--flip__card {
        display: flex;
        justify-content: center;
        top: 0;
        margin-top: 50px;
    }

    .flip--card__inner {
        display: flex;
        height: 90%;
        width: 600px;
        margin: 50px;
    }

    .btn--close__mobile {
        display: none;
    }

    .btn--close__mobile.btn--close__pc {
        display: block
    }

    .card--description .card--description__p {
        display: flex;
        flex: 1;
        font-size: 1.1rem;
        justify-content: flex-end;
    }

    .table--row__header {
        width: 100px;
    }

    .table--back__infor__row {
        font-size: 1.2rem;
    }

    .table--row__header {
        padding: 0;
        text-align: center;
        flex-wrap: wrap;
    }

    .wrapper--verify {
        width: 100%;
        height: 100%;
    }

    .verify--container {
        padding: 20px 10px;
        min-height: 100vh;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        object-fit: cover;
        display: flex;
        margin: auto;
        background-position: center center;
    }

    .verify--form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: auto;
        width: 500px;
        border-radius: 10px;
        background-color: var(--login-bg-color);
        padding: 65px 55px 54px;
    }

    .verify--form__title {
        font-size: 3rem;
        color: #333;
        line-height: 1.2;
        padding-bottom: 3rem;
    }

    .verify--description {
        padding-bottom: 20px;
        color: var(--red-color);
        font-size: 1rem;
    }

    .verify.btn--login {
        width: 80%;
        border-radius: 10px;
    }

    .group-modal .modal--contain {
        width: 90%;
        height: 90%;
        /* margin: 0 22px; */
    }

}

/*--Define general--*/
/*---------------------------CSS Mobile width < 740px--------------------------------*/
@media only screen and (max-width:46.25rem) {
    body {
        /* font-size: 0.8rem; */
        font-size: .6rem !important;
    }

    .login--container {
        padding: 0;
    }

    .login--form__title {
        font-size: 1.6rem;
        padding-bottom: 2rem;
    }

    .sign--form {
        width: 100%;
        padding: 40px 30px;
    }


    .signup--container {

        padding: 20px 10px;
    }

    .login--form {
        width: 100%;
        margin: 20px 10px;
        padding: 20px 30px;
        font-size: .6rem;
    }

    .wrapper--logger span {
        font-size: .7rem;
    }

    .login--form__policy {
        padding-bottom: 20px;
    }

    nav .container-fluid {
        padding: 10px 0;
    }

    .navbar--options__right {
        width: 200px;
    }

    .wrapper--navbar__list {
        width: 100%;
    }

    .navbar--list__btn {
        padding: 0 16px;
    }

    .navbar--list__controller {
        display: flex;
        padding: 10px;
        justify-content: space-between;
    }

    .navbar--list__controller .btn--close__mobile {
        font-size: 2.2rem;
        color: var(--red-color);
        padding: 10px 10px;
    }


    .flip--card__created__Datetime {
        color: var(--navbar-text-color);
        font-size: 1.2rem;
    }

    .table--row__header {
        width: 30px;
    }

    .table--back__infor__row {
        font-size: 1rem;
    }

    .table--row__header {
        padding: 0;
        text-align: center;
        flex-wrap: wrap;
    }

    .verify--form__title {
        font-size: 1.7rem;
    }

    .verify.btn--login {
        margin-top: 30px;
    }

    .verify--form {
        padding: 55px 20px 20px;
    }

    .verify--container {
        padding: 20px 10px;
        min-height: 100vh;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        object-fit: cover;
        display: flex;
        margin: auto;
        background-position: center center;
    }

    .post--header {
        font-size: .8rem;
    }

    .group-modal .modal--contain {
        margin: 0 22px;
        width: 95%;
    }

    .post--header__username,
    .post--header__datetime {
        overflow: ellipsis;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        overflow-wrap: break-word;
    }

    .wrapper--fields input {
        font-size: 16px;
    }

    .group-form .wrapper--input {
        width: 100%;
        display: flex;
        font-size: 16px;
    }

    .group-form .wrapper--input .input__fields {
        margin: 3px 5px;
        font-size: 16px;
    }

    .form-messege {
        padding: 0 12px;
    }

    .input--wrapper__messege {
        width: 80%;
    }
}